.side-drawer{
    height: 100%;
    background: white;  /* fallback for old browsers */
      
    box-shadow: 1px 0px 7px rgba(0, 0, 0, 0.5);
    position: fixed;
    top: 0;
    left: 0;
    width: 70%;
    max-width: 400px;
    z-index: 200;
    transform: translateX(-100%);
    transition: transform 0.3s ease-out;
    
}
.side-drawer.open{
    transform: translateX(0);
}
.side-drawer ul{
    padding-bottom: 50%;
    height: 100%;
    list-style: none;
  
    display: flex;
    flex-direction: column;
   
    
}
.side-drawer li{
    margin: 1.5rem 0;
    

}
.side-drawer a{
    color: black;
    text-decoration: none;
    font-size: 1rem;
    
    padding-left: 25px;
    
}
.side-drawer a:hover,
.side-drawer a:active{
    color: red;
}

